.tabs__list li.active {
    color: #ffffff;
    background-color: #B4864F;
    border: 1px solid #B4864F;
}

.tab__panels .tab__element.active {
   opacity: 1;
   height: auto;
   overflow: hidden;
}

.tab__panels .tab__element {
    opacity: 0;
    height: 0;
    overflow: hidden;
}

